        'Поздравляю с Днем рождения юную леди! Пусть этот мир откроется пред тобой самыми яркими красками! Пусть никогда не будет уныния в твоей душе! Умей учиться и умей отдыхать! Пусть в эти годы твое здоровье окрепнет, и ты превратишься в настоящую женщину! Пусть твои друзья и подруги помогают тебе, а ты помогай им!',
