          'Поздравляю тебя с днем рождения! От всей души хочу пожелать тебе счастья, здоровья, благополучия, а так же как можно больше начинаний, которые в дальнейшем завершатся так, как ты задумал! Пусть в твоей судьбе никогда не будет резких поворотов, а жизнь идет плавно и размеренно, при этом оставляя пространство для веселых приключений. Пусть тебе всегда везет и счастье в жизни никогда не кончается!',
          'Самое сильное и самое главное чувство – это любовь. Нельзя на свете прожить без любви. Я в этом убедилась, когда встретила тебя, мой любимый. Ты для меня много значишь. Каждой моя клеточка, каждое дыхание наполнено и заполнено тобой. Сегодня у тебя день рождения. Разреши от всего сердца поздравить тебя с таким прекрасным и большим праздником. Желаю тебе большого счастья, много радости в жизни, и всего самого наилучшего. Будь всегда любимым, желанным, будь просто моим самым главным человеком на свете.',
          'С днем варенья, пожелаю тебе быть любимой, чтобы все необходимое находилось рядышком с тобой, оставайся такой хорошей, пусть твое душевное состояние будет полно добра, света и радости. Будь такой, чтоб тобой восхищались еще сильнее, желаю осуществления твоих самых заветных желаний.'
      ]
    };
  },

  computed: {},

  watch: {},

  created() {
  },

